@charset "UTF-8";
.left{
  float: left;
}
.right{
  float: right;
}
.dialog-ctrl{
  position: absolute;
  right: 30px;
  top: 20px;
  cursor: pointer;
}
.icon-dialog-close{
  position: absolute;
  background: url("../images/icon-login-close.png") no-repeat;
  width: 12px;
  height: 12px;
}
/* <登录注册> */
.account-box{
  width: 318px;
  position: relative;
  padding: 30px 30px 10px;
  border: 1px solid #ececec;
  background-color: #fff;
}
.account-box input[type="email"].input-text{
  display: inline-block;
  background:url("../images/icon-regist-person.png") 10px no-repeat #fff !important;
  vertical-align: middle;
  text-indent: 30px;
  font-size: 14px;
}
.account-box input[type="password"].input-text{
  display: inline-block;
  background:url("../images/icon-regist-password.png") 10px no-repeat #fff !important;
  vertical-align: middle;
  text-indent: 30px;
  font-size: 14px;
}
.account-box form {
  padding-top: 5px;
}
.account-box .account-ctrl {
  margin-top: 15px;
  font-size: 14px;
  color:#666;
}
.account-box .account-ctrl .account-forget {
  font-size: 14px;
  color: #999;
}
.account-box .account-ctrl .clause-checkbox {
  display: none;
}
.account-box .account-ctrl .error .error-tips {
  display: block;
}
.account-box .messages .error-msg{
  color:#f30;
}
.account-creat{
  font-size: 14px;
  color:#999;
  margin-top: 5px;
  a{
    color:#1d6fb8;
    text-decoration:underline;
  }
}
.account-btn {
  margin-top: 0px;
}

.account-btn .btn {
  margin-top: 10px;
  width: 100%;
  border-radius: 0;
  font-size: 18px;
  color: #fff;
}
.account-btn .btn-back {
  border-color: #ececec;
  background-color: #fff;
  color: #777;
  font-weight: normal;
}
.account-btn .btn-back:hover {
  background-color: #777;
  color: #fff;
  text-decoration: none;
}
.input-group {
  margin-top: 20px;
}
.input-group.error .input-text {
  border-color: #f75354;
}
.input-group.error .error-tips {
  display: block;
}
.input-group .input-text {
  display: block;
  padding: 0 16px;
  width: 286px;
  height: 40px;
  line-height: 40px;
  border: 1px solid #ececec;
  outline: 0;
}

.error-tips {
  display: none;
  color: #f75354;
}
.error-tips .icon-error {
  display: inline-block;
  width: 16px;
  height: 16px;
  line-height: 16px;
  margin-right: 10px;
  font-style: normal;
  font-size: 12px;
  text-align: center;
  border: 1px solid #f75354;
  border-radius: 50%;
}

/* </登录注册> */
/* <联合登陆> */
.coop-login {
  margin-top: 28px;
}
.coop-login h3 {
  height: 18px;
  border-top: 1px solid #ddd;
  position: relative;
}
.coop-login h3 span {
  display: block;
  width: 160px;
  height: 18px;
  line-height: 18px;
  margin-left: -80px;
  background: #fff;
  font-size: 14px;
  color: #5f5f5f;
  text-align: center;
  position: absolute;
  left: 50%;
  top: -10px;
}
.coop-login .login-other {
  padding-left: 6px;
}
.coop-login .login-other .icon-union{
  display: inline-block;
  background: url("../images/icon-union.png") no-repeat;
  vertical-align: middle;
}
.coop-login .login-other a {
  float: left;
  margin: 10px 15px 20px;
  height: 42px;
  width: 42px;
  filter: alpha(opacity=70);
  opacity: .7;
  border:1px #c8c8c8 solid;
  border-radius: 5px;
}
.coop-login .login-other a:hover {
  filter: alpha(opacity=100);
  opacity: 1;
}
.coop-login .login-other .by-qq {
  background-position:0 0;
  width: 42px;
  height: 42px;
}
.coop-login .login-other .by-weibo {
  background-position:-42px 0;
  width: 42px;
  height: 42px;
}
.coop-login .login-other .by-alipay {
  background-position:-84px 0;
  width: 42px;
  height: 42px;
}
.coop-login .login-other .by-union {
  background-position:-126px 0;
  width: 42px;
  height: 42px;
}
/* </联合登录> */

